'use client';
import { useState, useEffect, useRef } from 'react';

export default function Rohbot() {
    const [isOpen, setIsOpen] = useState(false);
    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([
        { role: 'bot', content: "Hi! I'm Rohan's AI Twin. Ask me about his RAG projects or skills." }
    ]);
    const [loading, setLoading] = useState(false);
    const scrollRef = useRef(null);

    // Auto-scroll logic
    useEffect(() => {
        if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }, [messages]);

    const handleSendMessage = async (textOverride) => {
        const text = textOverride || input;
        if (!text.trim()) return;

        setMessages(prev => [...prev, { role: 'user', content: text }]);
        setInput('');
        setIsTyping(true);

        try {
            const res = await fetch('https://rohbot.vercel.app/api/chat', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    userInput: text,
                    history: messages.map(m => ({
                        role: m.role === 'user' ? 'user' : 'model',
                        parts: [{ text: m.content }]
                    }))
                })
            });

            // --- NEW DIAGNOSTIC CHECK ---
            if (!res.ok) {
                const errorData = await res.json().catch(() => ({}));
                console.error("SERVER ERROR:", res.status, errorData);
                throw new Error(`Server returned ${res.status}: ${JSON.stringify(errorData)}`);
            }

            if (!res.body) throw new Error("No response body received from server.");

            const reader = res.body.getReader();
            const decoder = new TextDecoder();
            let fullReply = "";

            setMessages(prev => [...prev, { role: 'bot', content: "" }]);

            while (true) {
                const { done, value } = await reader.read();
                if (done) break;
                const chunk = decoder.decode(value, { stream: true });
                fullReply += chunk;

                setMessages(prev => {
                    const newMsgs = [...prev];
                    newMsgs[newMsgs.length - 1].content = fullReply;
                    return newMsgs;
                });
            }
        } catch (e) {
            console.error("CHAT FETCH ERROR:", e);
            setMessages(prev => [...prev, { role: 'bot', content: `Error: ${e.message}` }]);
        } finally {
            setIsTyping(false);
        }
    };

    // --- Inline Styles for the Chatbot ---
    const styles = {
        button: {
            position: 'fixed', bottom: '30px', right: '30px', zIndex: 9999,
            width: '60px', height: '60px', borderRadius: '50%',
            backgroundColor: '#0070f3', border: 'none', cursor: 'pointer',
            boxShadow: '0 10px 30px rgba(0,0,0,0.5)', display: 'flex',
            alignItems: 'center', justifyContent: 'center', overflow: 'hidden'
        },
        window: {
            position: 'fixed', bottom: '100px', right: '30px', zIndex: 9999,
            width: '350px', height: '500px', backgroundColor: '#0d1117',
            border: '1px solid #333', borderRadius: '16px', display: 'flex',
            flexDirection: 'column', overflow: 'hidden', boxShadow: '0 20px 50px rgba(0,0,0,0.8)'
        },
        header: { padding: '15px', backgroundColor: '#161b22', borderBottom: '1px solid #333', fontWeight: 'bold', display: 'flex', justifyContent: 'space-between' },
        chatArea: { flex: 1, overflowY: 'auto', padding: '15px', display: 'flex', flexDirection: 'column', gap: '10px' },
        bubble: (role) => ({
            alignSelf: role === 'user' ? 'flex-end' : 'flex-start',
            backgroundColor: role === 'user' ? '#0070f3' : '#222',
            color: '#fff', padding: '10px 14px', borderRadius: '12px',
            fontSize: '14px', maxWidth: '80%', lineHeight: '1.5'
        }),
        inputArea: { padding: '15px', borderTop: '1px solid #333', display: 'flex', gap: '10px', backgroundColor: '#0d1117' },
        input: { flex: 1, backgroundColor: '#000', border: '1px solid #333', color: '#fff', padding: '10px', borderRadius: '8px', outline: 'none' },
        sendBtn: { backgroundColor: '#0070f3', border: 'none', color: '#fff', padding: '0 15px', borderRadius: '8px', cursor: 'pointer' }
    };

    return (
        <>
            <button style={styles.button} onClick={() => setIsOpen(!isOpen)}>
                {isOpen ? <span style={{fontSize: '24px', color: '#fff'}}>✕</span> :
                    <img src="../public/avatar-rohan.png" style={{width: '100%', height: '100%', objectFit: 'cover'}} alt="AI" />
                }
            </button>

            {isOpen && (
                <div style={styles.window}>
                    <div style={styles.header}>
                        <span>ROHbot (AI Twin)</span>
                        <span style={{color: '#22c55e', fontSize: '10px'}}>● ONLINE</span>
                    </div>

                    <div style={styles.chatArea} ref={scrollRef}>
                        {messages.map((m, i) => (
                            <div key={i} style={styles.bubble(m.role)}>{m.content}</div>
                        ))}
                        {loading && <div style={{fontSize: '11px', color: '#555'}}>Rohan's AI is typing...</div>}
                    </div>

                    <div style={styles.inputArea}>
                        <input
                            style={styles.input}
                            placeholder="Ask me..."
                            value={input}
                            onChange={(e) => setInput(e.target.value)}
                            onKeyDown={(e) => e.key === 'Enter' && handleSendMessage()}
                        />
                        <button style={styles.sendBtn} onClick={handleSendMessage}>
                            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="3"><line x1="22" y1="2" x2="11" y2="13"></line><polygon points="22 2 15 22 11 13 2 9 22 2"></polygon></svg>
                        </button>
                    </div>
                </div>
            )}
        </>
    );
}